// LAYOUT | src: /less/_layout.sass
// Layout rules are to lay elements on a page

// Main Content Wrapper
// This selector is used by main.js to move whole
// content to the side when navigating on mobile devices
// ------------------------------------------------------

.main-content {
	position: relative;
	z-index: 0;
	margin-top: 55px;
	@include font-smoothing;
	@include transition(transform .6s);
	&.nav-is-visible {
		@include transform(translateX(260px)); }
	// +desktop
	// 	&.subnav-open
	// 		// +transform(translateY(22px))
 }	// 		+transition(transform 3s)

// Form styling used on each page footer (see _themes.sass for style guide)
//--------------------------------------------------------------------------

.main-content {
	.fill-out-form {
		padding: 30px 0;
		background: #c5e1f7;
		h2 {
			margin: 0 auto;
			color: $brand-blue;
			margin-bottom: 20px;
			padding: 0 15px;
			text-align: center; }
		.roles-pages,
		.form-step-1 {
			@extend .col-xs-12;
			@extend .col-sm-offset-1;
			@extend .col-sm-10;
			@extend .col-lg-offset-2;
			@extend .col-lg-8;
			label {
				@extend .label-default;
				width: 100% !important; }
			input[type=text],
			input[type=e-mail],
			input[type=email],
			textarea {
				@extend .input-fields-default;
				background: $brand-white !important;
				color: $brand-blue !important; } }
		.form {
			margin: 0 auto;
			text-align: center; }
		.form-group {
			@extend .col-xs-12;
			@extend .col-sm-8;
			@extend .col-md-8;
			margin-bottom: 20px; }
		.form-group:last-child {
			@extend .col-sm-4;
			@extend .col-md-4;
			margin-bottom: 20px;
			text-align: center; }
		.form-control,
		.input-fields-default {
			&::-webkit-input-placeholder {
				color: $brand-blue !important; } }
		.sub {
			@extend .col-sm-offset-2;
			@extend .col-sm-8;
			color: $brand-navy-light;
			line-height: 1.3;
			text-align: center;
			margin-top: 0px;
			margin-bottom: 20px; }
		.sub > br {
			@extend .visible-lg; }
		@include tablet {
			h2 {
				padding: 0 44px; }
			.form-group {
				text-align: initial; }
			.form-group:last-child {
				text-align: left; } }
		@include desktop {
			h2 {
				padding: 0; }
			.form-group {
				input[type=e-mail],
				input[type=email],
				input[type=text] {
					width: 110%; } }
			.form-group:last-child {
				text-align: right; } } } }

// Marketo Form Customizations
// Let's beautify the simple Marketo form style with Tradeshift style guide
// ---------------------------------------------------------------------------
.modal {
	.modal-content {
		border-radius: 0px !important; }
	.modal-header {
		background: #ffffff;
		padding: 30px;
		padding-top: 0px;
		border: none;
		h3 {
			color: #00b0ff;
			font-size: 32px;
			line-height: 55px;
			font-weight: 300;
			letter-spacing: -0.02em;
			margin-top: 50px; }
		p.lead {
			line-height: 26px;
			font-size: 18px;
			color: #282828;
			padding: 0;
			margin: 0; }
		.close {
			margin-top: -44px !important;
			opacity: 0.8 !important;
			font-weight: 300 !important;
			font-size: 30px !important;
			margin-right: -11px; } }
	.modal-dialog {
		width: 500px; }
	.modal-body {
		padding: 15px;
		@include tablet {
			padding: 30px;
			padding-top: 0px; }
		.mktoForm {
			div.placeholder {
				display: none; } } }
	.modal-footer {
		padding: 30px; }
	.mktoForm {
		@extend .roles-pages;
		width: 100% !important;
		.mktoOffset,
		.mktoGutter,
		.mktoAsterix {
			display: none !important; }
		.mktoFieldWrap {
			@extend .form-group;
			float: none; }
		.mktoFormCol {
			float: none; }
		input[type=text],
		input[type=url],
		input[type=email],
		input[type=e-mail],
		input[type=tel],
		input[type=number],
		input[type=date],
		select.mktoField,
		textarea.mktoField {
			width: 100% !important;
			@extend .input-fields-default;
			@extend .form-control;
			background: #f2f2f2 !important;
			letter-spacing: initial !important;
			color: #3c3c3c !important;
			font-weight: 300 !important;
			border-radius: 6px !important;
			margin-bottom: 5px !important;
			&:focus {
				background: #f3f9fd !important;
				font-weight: 400 !important;
				border: 1px solid rgb(0, 176, 255) !important; } }
		.mktoLabel {
			@extend .label-default;
			width: 90% !important;
			display: none !important; }
		.mktoFieldWrap {
			label[for="Active_Project__c"] {
				width: 87% !important;
				float: right;
				font-size: 14px !important;
				line-height: 1.2 !important;
				text-transform: none !important;
				margin: 0;
				padding: 0; }
			@include tablet {
				label[for="Active_Project__c"] {
					width: 95% !important;
					padding-right: 30px; } } }
		.mktoLogicalField {
			input[name="Active_Project__c"] {
				font-size: 34px;
				opacity: 1; }
			label[for="Active_Project__c"] {
				display: none; } }
		.mktoError {
			left: 0 !important;
			right: initial !important; }
		.mktoButtonWrap {
			margin: 20px 0 0 0 !important; }
		.mktoButton {
			@extend .btn-primary-cta;
			padding: 0;
			font-family: $font-family-heading !important;
			background-image: none !important;
			background: #00b0ff !important;
			margin: auto !important;
			display: block !important; }
		.mktoButtonRow {
			width: 100% !important; }
		.mktoHtmlText {
			width: 90% !important;
			p.privacy {
				font-size: 12px !important;
				line-height: 18px !important; } }
		.mktoError {
			.mktoErrorArrow {
				border: none !important;
				background: $brand-red !important; }
			.mktoErrorMsg {
				background: $brand-red !important;
				border-radius: 0 !important;
				text-shadow: none !important;
				width: 180px !important;
				color: $brand-white !important; } } }
	@include phone {
		.modal-dialog {
			width: auto !important; }
		.modal-header {
			padding-bottom: 0px;
			h3 {
				font-size: 20px;
				line-height: 33px; }
			p.lead {
				font-size: 14px;
				line-height: 20px; } } } }

#SignUpModal.modal.modal-sign-up {
	.modal-dialog {
		width: 600px !important; }
	.modal-content {
		padding-top: 33px; }
	.modal-header {
		h3 {
			font-size: 33px !important;
			line-height: 48px !important;
			color: #00b0ff !important; } }
	.modal-body {
		input[type="checkbox"]+label span {
			background: #e6e7e8 !important;
			border: none !important;
			width: 17px !important;
			height: 17px !important; }
		input[type="checkbox"]:checked+label span {
			background: #ff9000 !important;
			border: 3px solid #e6e7e8 !important; }
		button.btn-primary-cta-ent {
			margin: 0 0 0 22px !important;
			background: #00b0ff !important;
			opacity: 1 !important;
			color: #fff !important;
			padding: 8px 44px;
			text-transform: initial !important;
			font-weight: 400 !important; } }
	@include phone {
		.modal-dialog {
			width: 320px !important;
			position: absolute !important;
			margin-left: auto !important;
			margin-right: auto !important;
			left: 0 !important;
			right: 0 !important;
			top: 66px !important; }
		.modal-header {
			padding: 0 22px !important;

			h3 {
				width: auto !important;
				font-size: 20px !important;
				line-height: 30px !important; } }
		.modal-content {
			padding-top: 11px !important;
			height: auto !important; }
		input[type="checkbox"]+label {
			font-size: 14px !important;
			line-height: 20px !important; } } }












