// Custom Styling for Home Page - Main - Ts.com
// Each page in the CMS is defined by an individual SASS page saved under /layout
// This is then imported into the _layout.sass folder to get included into the sitewide css

.rightSide {
	display: none; }
.box {
	float: right;
	position: relative;
	top: 0px;
	width: 200px; }
@keyframes fadeIn {
	0% {
		opacity: 0; }
	100% {
		opacity: 1; } }
body {
	animation: fadeIn 1.5s ease-out;
	position: relative;
	z-index: 2000; }
@include phone {
	.container {
		width: 320px; } }
@include desktop {
