		.tabCompList caption {
			width: 400px !important;
			overflow: hidden !important; } } }
main sup {
	top: -0.99em !important;
	font-size: 40% !important; }

.circle {
	border-radius: 50%;
	background-color: rgb(255, 255, 255);
	min-width: 62px;
	min-height: 62px;
	text-align: center;
	display: -ms-flexbox /* MS 10 */;
	display: -webkit-flex /* Safari 8 */;
	display: flex;
	-webkit-box-align: center /* align-items */;
	-ms-flex-align: center /* MS 10 */;
	align-items: center;
	-ms-flex-pack: center !important /* MS 10 */;
	-webkit-box-pack: center /* justify-content */;
	justify-content: center;
	svg {
		display: block;
		margin: auto;
		position: relative;
		left: 2px; }
	@media screen and (min-width:0\0) {
		/* IE9, IE10, IE11  rule sets go here */
		svg {
			margin-left: 0;
			margin-right: 0; } } }
.btn-white {
	color: #00b0ff !important;
	text-transform: inherit !important;
	font-weight: 600 !important;
	font-size: 18px !important; }
.home-main-v2 {
	// Button CTA used globally
	.btn-cta {
		background: $brand-s-apps !important;
		padding: 9px 44px !important;
		font-size: 18px !important;
		font-weight: 600 !important;
		height: 44px !important;
		margin: 0 auto !important;
		border: none !important;
		box-shadow: none;
		border-radius: 22px !important;
		letter-spacing: -0.01em;
		color: $brand-white !important;
		text-align: center !important;
		@include transition(background 200ms ease-in);
		&:hover {
			cursor: pointer; }
		&:hover,
		&:focus,
		&:active {
			background: $brand-p-apps !important;
			box-shadow: none;
			border: none;
			outline: none;
			opacity: 1; } }

	.btn-white {
		padding: 9px 44px !important;
		@include transition(all 100ms ease-in);
		&:hover {
			cursor: pointer; }
		&:hover,
		&:focus,
		&:active {
