// Table of Contents
// --------------------------------------------------
// 1.0 Bootstrap Core & Libraries - bootstrap styles from vendor folder sans normalize
// 2.0 Wordpress Core - styles called by WP - overwrite here if necessary
// 3.0 Utilities + Helpers + Mixins - helper classes: prefix by u-
// 4.0 Theme, incl. variables - pages look and feel: colors, typography, etc.
// 5.0 Base - base rules, no classes, no ids: body, html, a, etc.
// 6.0 Layout - lays the page down: header, article, section, aside, etc.
// 7.0 Components - Reusable styling - mostly sitewide header / footer etc.
// 8.0  Misc - other styles: hr, gradient mixins, etc.

// 1.0 Bootstrap Core & Libraries
// --------------------------------------------------
@import "bootstrap";
@import "bourbon";

// Marker to take out library from compiled CSS
// Only when separating styling for LTE IE9
// Damn that IE9 4095 selector limit rule
// --------------------------------------------------
.delete-above {
	display: initial; }

// 2.0 Wordpress Core
// --------------------------------------------------
@import "wp-classes";

// 3.0 Utilities + Helpers + Mixins
// --------------------------------------------------
@import "utilities";

// 4.0 Theme
// --------------------------------------------------
@import "theme-main";

// 5.0 Base
// --------------------------------------------------
@import "base";

// 6.0 Layout
// --------------------------------------------------
@import "layout-main";
@import "layout/_main-front-page";

// 7.0 Components
// --------------------------------------------------
@import "components-main-slide-out";
//@import components-main-v4-solutions
// 8.0 Misc
// --------------------------------------------------
@import "misc-main";



